<style lang="less">
@import "./login.less";
</style>
<template>
<mu-container class="login">
    <div class="logo">
        <img src="../../assets/imgs/logo_xny.png">
    </div>
    <div class="title">
        BYD移动终端销售系统
    </div>
   <mu-form ref="form" :model="loginForm" class="mu-demo-form">
    <mu-form-item label="请输入用户名" :label-float="true" prop="username" :rules="usernameRules">
      <mu-text-field v-model="loginForm.username" prop="username"></mu-text-field>
    </mu-form-item>
    <mu-form-item label="请输入密码" :label-float="true"  prop="password" :rules="passwordRules">
        <mu-text-field type="password" v-model="loginForm.password" prop="password"></mu-text-field>
    </mu-form-item>
    <mu-form-item class="submit">
      <mu-button color="primary" style="width:200px" @click="submit">提交</mu-button>
    </mu-form-item>
  </mu-form>
</mu-container>
</template>

<script>
export default {
  data() {
    return {
      loginForm: {
        username: "",
        password: ""
      },
      usernameRules: [
        { validate: val => !!val, message: "请输入用户名" },
        { validate: val => val.length >= 3, message: "用户名长度大于3" }
      ],
      passwordRules: [
        { validate: val => !!val, message: "请输入密码" },
        {
          validate: val => val.length >= 3 && val.length <= 10,
          message: "密码长度大于3小于10"
        }
      ]
    };
  },
  methods:{
      submit(){
          localStorage.setItem('user',1111);
          this.$router.replace('/');
      }
  }
};
</script>



