import appContext from './context'
import http from './http'
import qs from 'qs'
import appConfig from '../config'

export default {
  baseSvc: {
    post (url, oData) {
      return http.post(appContext.buildBaseUrl(url), oData)
    },
    uploadImgUrl () {
      return appContext.buildImgUploadUrl()
    },
    showImgUrl (img) {
      return `${appConfig.baseUrl}/${img}`
    },
    login (oData) {
      return http.post(appContext.buildBaseUrl(`userManager/backendlogin/login`), qs.stringify(oData))
    }
  }
}



