import Vue from 'vue'
import Vuex from 'vuex'
import Cookies from 'js-cookie'

Vue.use(Vuex)
const store = new Vuex.Store({
  // 定义状态
  state: {
    userID: 0,
    user: null,
    menus: [],
    subMenus: [],
    loading: false,
    fixed: false,
    token: '',
    title: '',
    isSubPage:false,
    hasPermission: function (name) {
      return this.user && this.user.permissions ? this.user.permissions.find(function (f) {
        return f.name === name
      }) != null : false
    },
    categoryId: 0,
    productId: 0
  },
  mutations: {
    newAuthor (state, msg) {
      state.author = msg // 在组件中,通过this.$store.commit('newAuthor','value') 改变其值
      // 在created中,通过this.$store.state.author 来获取值
    },
    setUser (state, user) {
      state.user = user
      state.userID = user ? user.userId : 0
    },
    setMenus (state, menus) {
      state.menus = menus
      state.fixed = false
    },
    setSubMenus (state, menus) {
      state.subMenus = menus
    },
    logout (state, vm) {
      Cookies.remove('access-token')
      Cookies.remove('token-type')
    },
    setTitle (state, title) {
      state.title = title
      dsBridge.call('setNaviBarTitle', title)
    },
    setSubPage(state,isSubPage){
      state.isSubPage = isSubPage;
    }
  }
})

export default store



