/**
 * Created by Norton Li on 2018/06/11.
 * http配置
 */
import axios from 'axios'
import store from '../store.js'
import router from '../routers.js'
import Toast from 'muse-ui-toast'
import Cookies from 'js-cookie'

// axios 配置
axios.defaults.timeout = 5000
// http request 拦截器
axios.interceptors.request.use(
  config => {
    let token = Cookies.get('access-token')
    let tokenType = Cookies.get('token-type')
    if (token) {
      config.headers.Authorization = `${tokenType || 'jwt'} ${token}`
    }
    store.state.loading = true
    return config
  },
  err => {
    return Promise.reject(err)
  })

// http response 拦截器
axios.interceptors.response.use(
  response => {
    store.state.loading = false
    if (response.status === 204) {
      switch (response.config.method.toUpperCase()) {
        case 'DELETE':
        {
          Toast.info('指定的项目已删除成功。')
          return
        }
        case 'GET':
        case 'POST':
        {
          Toast.warning('指定的项目不存在或已经被删除。')
          return
        }
        default:
          return
      }
    }

    return response
  },
  error => {
    store.state.loading = false
    if (error.response) {
      console.log(error.response)
      switch (error.response.status) {
        case 401:
          // 401 清除token信息并跳转到登录页面
          store.commit('logout', this)
          Toast.error('请重新登录')
          dsBridge.call('noticeTokenExpires')
          return
        case 403:
          router.replace({
            path: 'error/403'
          })
          return
        case 400:
        case 500:
          Toast.error('系统内部错误，请联系系统支持小组。')
          return
        case 501:
          Toast.warning('此功能尚未实现。')
          return
        default:
          return
      }
    }

    Toast.error('系统错误，请联系系统支持小组。')
    return Promise.reject(error)
  })

export default {
  get (url, data, paramsSerializer) {
    return axios.get(url, {
      params: data,
      paramsSerializer: paramsSerializer || null
    }).then(res => {
      return res
    })
  },
  post (url, data) {
    let p = axios.post(url, data).then(res => {
      if (res && res.data) {
        if (res.data.code === 200) {
          if (!p._disableToast) {
            // Toast.success('操作成功！')
          }
        } else {
          Toast.error(res.data.message)
        }
      }
      return res
    })
    p._disableToast = false
    return p
  },
  delete (url, data) {
    let p = axios.delete(url, {
      params: data
    }).then(res => {
      if (res && !p._disableToast) {
        Toast.success('删除成功！')
      }
      return res
    })
    p._disableToast = false
    return p
  },
  upload (url, data) {
    return axios.post(url, data, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    }).then(res => {
      return res
    })
  }
}



