<style lang="less">
@import "./clue.less";
</style>
<template>
<mu-container class="newclue">
    <mu-tabs class="tab" :value.sync="active" color="#8a8a8a" text-color="rgba(0, 0, 0, .54)">
        <mu-tab>基础信息</mu-tab>
        <mu-tab>意向车型</mu-tab>
        <mu-tab>联系人</mu-tab>
    </mu-tabs>
    <div v-if="active===0">基础信息</div>
    <div v-if="active===1">意向车型</div>
    <div v-if="active===2">联系人</div>
</mu-container>
</template>
<script>
export default {
    data(){
        return {
            active:0
        }
    }
};
</script>



