import Vue from 'vue'
import VueRouter from 'vue-router'
import Cookies from 'js-cookie'
import Util from './libs/util'
import store from './store'

import clueIndex from '@/views/clue/index'
import clueNew from '@/views/clue/new'

import connectionIndex from '@/views/connection/index'
import customerIndex from '@/views/customer/index'
import meIndex from '@/views/me/index'
import Main from '@/views/layout/main'
import login from '@/views/login/index'

Vue.use(VueRouter)

const loginRouter = {
  path: '/login',
  meta: {
    title: '登录',
  },
  name: 'login',
  component: login
}

const routes = [{
  path: '/',
  name: 'main',
  redirect: '/clue',
  component: Main,
  children: [{
    path: '/clue',
    meta: {
      title: '线索',
      requiredAuth: true
    },
    name: 'clue',
    component: clueIndex
  },
  {
    path: '/clue/new',
    meta: {
      title: '新增线索',
      requiredAuth: true,
      subPage:true
    },
    name: 'clue/new',
    component: clueNew
  },
  {
    path: '/connection',
    meta: {
      title: '人脉',
      requiredAuth: true
    },
    name: 'connection',
    component: connectionIndex
  },
  {
    path: '/customer',
    meta: {
      title: '客户',
      requiredAuth: true
    },
    name: 'customer',
    component: customerIndex
  },
  {
    path: '/me',
    meta: {
      title: '我的',
      requiredAuth: true
    },
    name: 'me',
    component: meIndex
  },
  ]
},
  loginRouter]

const router = new VueRouter({
  routes: routes
})

router.beforeEach((to, from, next) => {
  store.commit('setTitle', to.meta.title)
  store.commit('setSubPage', to.meta.subPage)
  if (!localStorage.getItem('user') && to.meta.requiredAuth) {
    // dsBridge.call('getAccessToken', function (result) {
    //   Cookies.set('access-token', JSON.parse(result).data.token)
    //   next()
    // })
    next('login')
  } else {
    Util.toDefaultPage([...routes], to.name, router, next)
  }
})
router.afterEach((to) => {
  window.scrollTo(0, 0)
})

export default router



