<style lang="less">
@import "./me.less";
</style>
<template>
<mu-container class="me">
  <mu-card style="width: 100%;min-height:80vh;">
  <mu-card-header title="张三" sub-title="销售经理">
    <mu-avatar size="60" slot="avatar">
      <mu-icon size="36" color="#eee" value=":fa fa-user-o"></mu-icon>
    </mu-avatar>
  </mu-card-header>
  <mu-card-title title="深圳比亚迪" sub-title=""></mu-card-title>
  <div class="operation">
      <mu-button color="primary" style="width:200px" @click="updatePwd">修改密码</mu-button>
      <mu-button color="primary" style="width:200px" @click="logout">退出登录</mu-button>
  </div>
  </mu-card>
</mu-container>
</template>

<script>
export default {
  data(){
    return{}
  },
  methods:{
    logout(){
      localStorage.removeItem('user');
      this.$router.replace('/')
    },
    updatePwd(){}
  }
};
</script>



