<template>
<div style="height: 100%;">
    <div>
        <mu-appbar style="width: 100%;" color="primary" v-if="$store.state.isSubPage">
            <mu-button icon slot="left" @click="$router.back(-1)">
                <mu-icon value=":fa fa-angle-left"></mu-icon>
            </mu-button>
            {{$store.state.title}}
        </mu-appbar>
        <!-- <mu-fade-transition> -->
        <keep-alive>
            <router-view v-if="$route.meta.keepAlive"></router-view>
        </keep-alive>
        <router-view v-if="!$route.meta.keepAlive"></router-view>
        <!-- </mu-fade-transition> -->
        <mu-bottom-nav class="footer" v-if="!$store.state.isSubPage">
            <mu-bottom-nav-item title="线索" to="/clue" icon=":fa fa-user-plus"></mu-bottom-nav-item>
            <mu-bottom-nav-item title="人脉" to="/connection" icon=":fa fa-address-book-o"></mu-bottom-nav-item>
            <mu-bottom-nav-item title="客户" to="/customer" icon=":fa fa-users"></mu-bottom-nav-item>
            <mu-bottom-nav-item title="我的" to="/me" icon=":fa fa-user-o"></mu-bottom-nav-item>
        </mu-bottom-nav>
    </div>
</div>
</template>

<script>
export default {
    data() {
        return {
            activeName: "",
            shrink: false,
            isShow: false
        };
    },
    mounted() {},
    computed: {},
    watch: {
        $route(now, old) {
            if (now.path === "/") {
                this.isShow = false;
            } else {
                this.isShow = true;
            }
        }
    },
    methods: {
        goBack() {
            this.$router.back(-1);
        },
        closePage() {
            dsBridge.call("closeCurPage");
        }
    }
};
</script>

<style>
.mu-bottom-nav .mu-icon {
    font-size: 16px;
}
</style>



